.scrollBar {
  width: 100%;
}
.input {
  -webkit-appearance: none;
  appearance: none;
  width: 100%;
  height: 0.3rem;
  background-color: #a62b2b;
  outline: none;
}

.input::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 0.5rem;
  height: 1rem;
  background-color: #a62b2b;
  border: none;
  cursor: pointer;
}
.input::-moz-range-thumb {
  appearance: none;
  width: 0.5rem;
  height: 1rem;
  background-color: #a62b2b;
  border: none;
  cursor: pointer;
}
.input::-ms-thumb {
  appearance: none;
  width: 0.5rem;
  height: 1rem;
  background-color: #a62b2b;
  border: none;
  cursor: pointer;
}

.text {
  margin: 0.5rem;
  font-family: "Gowun Batang", serif;
  color: #494949;
  font-weight: bold;
}
