* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body,
#root,
.drumMachine {
  width: 100%;
  height: 100%;
}

.drumMachine {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  background-color: #fcf9e6;
}

.radial-accent {
  background: radial-gradient(#a62b2b, #494949) !important;
}

.footer {
  position: fixed;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #fcf9e6;
  border-top: 2px solid #a62b2b;
  width: 100%;
  height: 1.5rem;
}

.footer .bukulele-logo {
  margin: 1px;
  height: 80%;
  width: auto;
}
.footer p {
  font-size: 0.8rem;
  color: #a62b2b;
}
.footer a {
  height: 100%;
}

@media only screen and (max-width: 480px) {
  .drumMachine {
    flex-direction: column;
  }
}

